<template>
  <div class="order-receivable">    
    <showGraph></showGraph>
    <template>
      <el-row :gutter="20">
        <el-col :span="24">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>问题</span>
              <el-button style="float: right; padding: 3px 0" type="text" @click="reqQA()">提问</el-button>
            </div>
            <div>
              <el-input
                type="textarea"
                autosize
                placeholder="请输入内容"
                v-model="input_text">
              </el-input>
            </div>
          </el-card>

          </br>
          
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>实体匹配</span>
            </div>
            <el-row>
              <el-button
                size="small"
                type="primary"
                @click="openGraph(value,label[0])"
                v-for="(label,value) in entities">
                  {{value}}:{{label[0]}}
              </el-button>
            </el-row>
          </el-card> 

          </br>
          
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>答案</span>
            </div>

            <el-col>
              <div v-for="(i,index) in recommand_text">             
                <div style="margin-bottom:10px" v-for="(value,key,index) in i">                  
                  <h5>{{key}}</h5>
                  <p class="text item" v-for="(j,index) in value">
                    {{j}}
                  </p>
                </div>                          
              </div>
            </el-col>
          </el-card>          
        </el-col>  


      </el-row>
    </template>
  </div>
</template>

<script>
  import {getOrderReceivable, reqQA, entityGraph} from '../../axios/api' 
  import showGraph from '../../components/dialog/showGraph'
    export default {
        name: 'recommand',
        components: {
          showGraph
        },
        data () {
            return {
              input_text: '',
              recommand_text: '',
              entities: {},              
            }
        },
        mounted(){
        },
        methods: {
          openGraph(value,label){
            entityGraph(value,label).then(res=>{
              this.$store.state.graph_data = res.data
            })      
                
            this.$store.dispatch('dialogshowGraph')
          },          
          
          reqQA(){
            if(this.input_text == "") return;
            reqQA(this.input_text).then(res=>{
              if(res.data == 'not found'){
                  alert("没有匹配的推荐内容");
                  return;
                }
                this.entities = res.data.entities;
                this.recommand_text = res.data.recommand;
            })
          },         
        }
    }
</script>
<style scoped>
  .el-row .el-date-editor,.el-input,.el-select{
    width: 100%;
    max-width: 300px;
  }
  .el-form-item{
  margin-bottom:0;
}
  .container .el-row{
    height:auto;
  }
  .el-row{
    margin-bottom: 0;
  }
  h5{
    margin: 0;
  }
  .el-table{
    margin-top: 8px;
  }
  .text {
    font-size: 14px;
  }

  .item {
    padding: 3px;
  }
</style>



